import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { TenantOptionsService } from '@c8y/client';
import { TrustedCertificatesGuard } from './trusted-certificates.guard';

@Injectable()
export class TrustedCertificatesNavigationFactory implements NavigatorNodeFactory {
  nav: NavigatorNode[] = [];

  constructor(
    private tenantOptionsService: TenantOptionsService,
    private trustedCertificatesGuard: TrustedCertificatesGuard
  ) {}
  async get() {
    const isFeatureEnabled = await this.trustedCertificatesGuard.isFeatureEnabled();
    if (isFeatureEnabled && this.nav.length === 0) {
      this.nav.push(
        new NavigatorNode({
          label: gettext('Trusted certificates'),
          icon: 'certificate',
          path: '/trusted-certificates',
          parent: gettext('Management'),
          priority: 100
        })
      );
    }
    return this.nav;
  }
}
