import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot } from '@angular/router';
import { OptionsService } from '@c8y/ngx-components';

@Injectable()
export class TrustedCertificatesGuard implements CanActivate {
  private isFeatureEnabledCache: boolean;
  constructor(private optionsService: OptionsService) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    return this.isFeatureEnabled();
  }

  async isFeatureEnabled() {
    if (this.isFeatureEnabledCache === undefined) {
      this.isFeatureEnabledCache = (await this.optionsService.getTenantOption(
        'oauth.internal.token',
        'trusted-certificates.enabled',
        false
      )) as boolean;
    }
    return this.isFeatureEnabledCache;
  }
}
